*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body {
  font-family: 'Open Sans', sans-serif;
  display: flex;
  padding: 2rem;
  background-color: #e9e7ee;
  font-family: "Inter", sans-serif;
}

.app-container {
  display: grid;
  grid-gap: 1em;
  grid-template-columns: 1fr  3fr;
  width: 100%;
}

.config {
  grid-column: 1 / 2 ;
  grid-row: 1;
  display: grid;
  border: 1px solid rgb(78, 107, 187);
  background-color: rgb(255, 255, 255);
  box-shadow: 2px 3px #888888;
  justify-items: center;

}

.config h2 {
  padding: 1rem 0 1rem;
  text-align: center;
  border-bottom: 1px solid rgb(78, 107, 187);
}

.config h3 {
  padding: 1rem 0 3rem;
  text-align: center;
  
}

.config h6 {
  text-align: center;
  padding: 1rem 0 1rem;
  
}

.config br {
  padding: 1rem 0 3rem;
}

.config-item-input{
  padding: 1rem 0 0rem;
  grid-column: 1 / -1;
}

.config input{
  width: 100%;
  grid-auto-flow: column;
  padding: 5px 10px;
}

.config button {
  width: 100%;
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.message {
  grid-column: 1 / 2 ;
  grid-row: 2;
  display: grid;
  border: 1px solid rgb(78, 107, 187);
  background-color: rgb(255, 255, 255);
  box-shadow: 2px 3px #888888;
  width: 500px;
}

.message h2 {
  padding: 1rem 0 1rem;
  text-align: center;
  border-bottom: 1px solid rgb(78, 107, 187);
}

.message h3 {
  padding: 2rem 0 1rem;
  text-align: center;
}

.code {
    grid-column: 1 / span 2;
    color: rgb(230, 236, 241);
    display: block;
    hyphens: none;
    tab-size: 2;
    direction: ltr;
    font-size: 11px;
    text-align: left;
    overflow-wrap: break-word;
    word-wrap: break-word;
    word-break: break-all;
    font-family: "Source Code Pro", Consolas, Menlo, Monaco, Courier, monospace;
    line-height: 1.4;
    white-space: pre;
    word-spacing: normal;
    margin: 10px 7px;
    padding: 24px 24px 24px 8px;
    overflow: auto;
    background: rgb(24, 48, 85);
    border-radius: 3px;
    height: 375px;
    outline: none;
}

.mobile {
  grid-column: 2;
  grid-row: 1 / 3;
  position: relative;
  width: 400px;
  height: 750px;
  margin: 2rem auto 2rem;
  padding: 40px 24px;
  background-color: #fffdfd;
  border: 1px solid rgb(63, 62, 62);
  border-radius: 56px;
  box-shadow: 5px 7px #888888;
}

.mobile__screen {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100%;
  background-color: rgb(223, 223, 226);
}

.mobile__scroll-cont {
  overflow-y: scroll;
  position: absolute;
  left: 0;
  top: -21px;
  width: calc(100% + 15px);
  height: calc(100% + 42px);
  padding: 21px 0;
}

.mobile__content {
  position: relative;
  padding-top: 30px;
}

.mobile-container {
  display: grid;
  grid-template-columns: 1fr;
 
  grid-gap: 10px;
  padding: 10px;
}

.mobile-item {
  display: grid;
  justify-content: center;
  align-items: center;
  border: 1px solid rgb(78, 107, 187);
  background-color: rgb(255, 255, 255);
  box-shadow: 2px 3px #888888;
}

.mobile-item h2 {
  padding: 1rem 0 1rem;
  text-align: center;
}

.mobile-item-detail{
  display: grid;
  grid-template-columns: 100px 100px 100px;
  grid-gap: 10px;
  background-color: #fff;
  color: #444;
}

.mobile-item-id {
  grid-column-start: 1;
  grid-column-end: 3;
  padding: 1rem 2rem 1rem;
}

.mobile-item-type {
  grid-column-start: 3;
  grid-column-end: 5;
  padding: 1rem  1rem;
  justify-content: end;
}

.mobile-item-button {
  grid-column-start: 2;
  grid-column-end: 3;
  padding: 1rem 1rem 1rem;
  justify-content: center;
  width: 100%;
  background-color: rgb(62, 65, 238);
  color: white;
  padding: 14px 20px;
  margin: 8px 0px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.mobile-item p {
  padding: 1rem 0 1rem;
  margin: 10px 10px 10px;
  line-height: 1.5;
  text-align: center;
}

.mobile-item-fur {
  grid-column-start: 2;
  grid-column-end: 3;
  justify-content: center;
  padding: 1rem 0 1rem;
  margin: 10px 10px 10px;
  line-height: 1.5;
  text-align: center;
}
